<template>
	<div class="stage-win pt20 pl10 pr10">
		<div class="clearfix">
			<span class="txt-color">画布背景色:</span>
			<el-color-picker v-model="bgColor" @change="_bgColorChange" size="mini"></el-color-picker>
		</div>

		<switch-txt-block 
			:switchName="switchName" 
			:switchVal="getCurrentFileById.fileBg.isTransport" 
			@change="_change">
		</switch-txt-block>

		<div class="pt20 clearfix">
			<text-left-input class="fl" 
				:hasPL="false" 
				:inputName="'宽度'" 
				:inputVal="getCurrentFileById.width"
				ref="widthInput"
				:disable="true"
				@zfblur="(val)=>{_inputBlur(val, 'widthInput', 'width')}"
				:inputWidth="inputWidth">
			</text-left-input>

			<text-left-input class="fl" 
				:hasPL="true"
				:inputName="'高度'"
				:inputVal="getCurrentFileById.height" 
				ref="heightInput"
				@zfblur="(val)=>{_inputBlur(val, 'heightInput', 'height')}"
				:inputWidth="inputWidth">
			</text-left-input>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';
	import ColorSet from '../../ColorSet/ColorSet.vue';
	import SwitchTxtBlock from '../../SwitchGroup/TxtBlock';
	import TextLeftInput from '../../inputGroup/TxtLeft.vue';

	export default{
		data() {
			return {
				switchName: '背景是否透明:',
				inputWidth: 55,
				bgColor: '#fff'
			}
		},

		computed: {
			...mapGetters(['getCurrentFileById'])
		},

		methods: {
			_change(val) {
				this.$store.dispatch('setStageTransport', val);
			},

			_bgColorChange(color) {
				this.$store.dispatch('setStageColor', color);
			},

			_bgStoreColorChange() {
				this.bgColor = this.getCurrentFileById.fileBg.bgColor;
			},

			_inputBlur(val, refName, attr) {
				let _this = this;
				let re = /^(\-)?\d+(\.\d+)?$/;

				if(re.test(val)) {
					// 宽高设置
					if(parseInt(val, 10) <= _this.getCurrentFileById.minWidth) {
						this.$message({
							showClose: true,
							message: '最小值为' + _this.getCurrentFileById.minWidth + 'px',
							type: 'error'
				        });

						// 负数
						if(attr == 'width') {
							this.$store.dispatch('setStageData', {
								width: _this.getCurrentFileById.minWidth
							});
						}else{
							this.$store.dispatch('setStageData', {
								height: _this.getCurrentFileById.minHeight
							});
						}
					}else{
						// 正数
						if(attr == 'width') {
							this.$store.dispatch('setStageData', {
								width: parseInt(val, 10)
							});
						}else{
							this.$store.dispatch('setStageData', {
								height: parseInt(val, 10)
							});
						}
					}
				}else{
					this.$refs[refName].setVal(this.getCurrentFileById[attr]);
					this.$message({
						showClose: true,
						message: '输入必须为数值',
						type: 'error'
			        });
				}
			}
		},

		components: {
			ColorSet,
			SwitchTxtBlock,
			TextLeftInput
	    },

	    created() {
	        
	    },

	    mounted() {
	        this.bgColor = this.getCurrentFileById.fileBg.bgColor;
	    },

	    watch: {
	    	'getCurrentFileById.fileBg.bgColor': '_bgStoreColorChange'
	    }
	}
</script>

<style lang="scss">
	.stage-win{
		.bgcolor{
			color: #fff;
			font-size: 12px;
		}

		.txt-color{
			display: inline-block;
			vertical-align: middle;
			font-size: 11px;
	    	color: #fff;
		}

		.el-color-picker{
			vertical-align: middle;
		}
	}
</style>


